	module.exports = {"data":{"markdownRemark":{"html":"<p>\"Le parler en langue\" est l’un des thèmes les plus controversés dans le christianisme mais c’est pourtant un sujet simple à comprendre. Plusieurs théories ont été élaborées sur le sujet. Les mouvements pentecôtiste et charismatique sont ceux qui ont plus mis ce phénomène en exergue.</p>\n<p>Ce site n'a pas été réalisé par une église ou une religion mais par deux jeunes amoureux de la Bible, désireux de partager les lumières qu'ils y ont découvert sur ce sujet avec ceux qui se posent des questions.</p>\n<p>Si vous avez des questions, n'hésitez pas à nous contacter nous serions ravis d'en discuter.</p>","frontmatter":{"title":"À propos"}}},"pathContext":{"slug":"/a-propos/"}}

